import {
  mapping as all,
  DEFAULT_BULLET_LIST_ARRAY,
  DEFAULT_NUMBER_LIST_ARRAY,
} from './listStyleMapping';
import { ListStyleProperties } from './types';
import { transformNumber } from './transformNumber';

export const RELATIVE_LEVEL = [0, 1, 2];
const REG_DEC_DEC_DEC = /%\d(.%\d)+/;

const isContinuousDec = (listStyle?: ListStyleProperties) => {
  // 处理导入没有 listStyleType 的 连续数字类型(如：2.1.1)的列表
  return listStyle?.format === 'decimal' && REG_DEC_DEC_DEC.test(listStyle.text);
};

const getListSymbolTuple = (
  isOrdered: boolean,
  relativeLevel: number,
  listStyle?: ListStyleProperties,
): string[] => {
  if (listStyle) {
    const { format, text, align } = listStyle;
    return [format, text, align];
  } else {
    const listTypeName = isOrdered
      ? DEFAULT_NUMBER_LIST_ARRAY[0]
      : DEFAULT_BULLET_LIST_ARRAY[0];
    return all[listTypeName][relativeLevel];
  }
};

export function generateSymbol(
  level: number,
  start: number,
  step: string,
  isOrdered: boolean,
  listStyleType?: string,
  listStyle?: ListStyleProperties,
): string {
  const remainder = level % 3;
  // 旧列表的level存在负数跟小数会导致all[listTypeName][relativeLevel]返回的是undefined，数组结构失败，所以用RELATIVE_LEVEL处理
  const relativeLevel = RELATIVE_LEVEL.includes(remainder) ? remainder : 0;
  const listSymbolTuple = getListSymbolTuple(
    isOrdered,
    relativeLevel,
    listStyle,
  );
  const [type, pattern] = listSymbolTuple;
  let listSymbol = pattern || '';
  // process number symbol, if match %number pattern
  // TODO 暂时保留listStyleType字段，未来依赖上下文判断列表类型
  if (listStyleType === DEFAULT_NUMBER_LIST_ARRAY[2] || isContinuousDec(listStyle)) {
    return `${step}.`;
  } else if (RegExp(/%\d/).test(listSymbol)) {
    listSymbol = listSymbol.replace(/(%\d)/g, () => transformNumber(start, type));
  }
  return listSymbol;
}